
.formWrapper label {
  display: inline-block;
  width: 60px;
  text-align: right;
}
.formWrapper input[type=text] {
  width: 200px;
  border: 1px solid #e5e5e5;
  margin-left: 1rem;
}
.formWrapper input[type=password] {
  width: 200px;
  border: 1px solid #e5e5e5;
  margin-left: 1rem;
}
.formWrapper input[type=submit] {
  display: inline-block;
  width: 60px;
  height: 32px;
  line-height: 32px;
  text-algin: center;
  border: none;
}
.formWrapper .actions {
  margin-left: 60px;
}



