
.page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  background: #e5e5e5;
}
.page>section {
  min-width: 1024px;
  max-width: 1440px;
  margin-top: 16px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  -ms-flex-item-align: center;
      align-self: center;
}
#resumeEditor {
  width: 35%;
  background: #444;
}
#resumePreview {
  width: 60%;
  background: #777;
}
svg.icon{
  height: 1em;
  width: 1em;
  fill: currentColor;
  vertical-align: -0.1em;
  font-size:16px;
}



